<template>
  <div class="history-detail">
    <div class="content flexStart">
      <div class="left">
        <Form :label-width="100">
          <FormItem label="操作时间">
            <label>{{arr1.updateTime.substr(0,10)}}</label>
          </FormItem>
          <FormItem label="业务类型">
            <label>{{arr1.busType}}</label>
          </FormItem>
          <FormItem label="业务名称">
            <label>{{arr1.busName}}</label>
          </FormItem>
          <FormItem label="产品代码">
            <label>{{arr1.proCode}}</label>
          </FormItem>
          <FormItem label="产品名称">
            <label>{{arr1.proName}}</label>
          </FormItem>
          <FormItem label="所属业务部门">
            <label>{{arr1.department}}</label>
          </FormItem>
          <FormItem label="成立日期">
            <label>{{arr1.buildDate?arr1.buildDate.substr(0,10):''}}</label>
          </FormItem>
          <FormItem label="期限">
            <label>{{arr1.term}}</label>
          </FormItem>
          <FormItem label="是否允许境外客户">
            <label>{{arr1.abroad}}</label>
          </FormItem>
          <FormItem label="是否业务投资境外">
            <label>{{arr1.investmentMark}}</label>
          </FormItem>
          <FormItem label="产品描述">
            <label>{{arr1.proDescribe}}</label>
          </FormItem>
        </Form>
      </div>
      <div class="line"></div>
      <div class="right">
        <Form :label-width="100">
          <FormItem label="操作时间">
            <label>{{arr2.updateTime.substr(0,10)}}</label>
          </FormItem>
          <FormItem label="业务类型">
            <label>{{arr2.busType}}</label>
          </FormItem>
          <FormItem label="业务名称">
            <label>{{arr2.busName}}</label>
          </FormItem>
          <FormItem label="产品代码">
            <label>{{arr2.proCode}}</label>
          </FormItem>
          <FormItem label="产品名称">
            <label>{{arr2.proName}}</label>
          </FormItem>
          <FormItem label="所属业务部门">
            <label>{{arr2.department}}</label>
          </FormItem>
          <FormItem label="成立日期">
            <label>{{arr2.buildDate?arr2.buildDate.substr(0,10):''}}</label>
          </FormItem>
          <FormItem label="期限">
            <label>{{arr2.term}}</label>
          </FormItem>
           <FormItem label="是否允许境外客户">
            <label>{{arr2.abroad}}</label>
          </FormItem>
          <FormItem label="是否业务投资境外">
            <label>{{arr2.investmentMark}}</label>
          </FormItem>
          <FormItem label="产品描述">
            <label>{{arr2.busDescribe}}</label>
          </FormItem>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["dataObjj"],
  created() {
    var that = this;
    let data = {
      id: this.dataObjj.id,
      newId: this.dataObjj.newId
    };
    data = JSON.stringify(data);
    this.axios
      .post("/risk/pro/proRiskInfoHisComPare", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
            
          if (response.data.length > 1) {
             
            that.arr1 = response.data[0];
            that.arr2 = response.data[1];
          } else if (response.data.length > 0) {
           
            that.arr1 = response.data[0];
          }
        }
      })
      .catch(function(error) {});
  },
  data() {
    return {
      arr1: 
        {
          updateTime: "",
          busTypeName: "",
          busCode: "",
          busName: "",
          dpname: "",
          createDate: "",
          busDescribe: ""
        }
      ,
      arr2: 
        {
          updateTime: "",
          busTypeName: "",
          busCode: "",
          busName: "",
          dpname: "",
          createDate: "",
          busDescribe: ""
        }
      
    };
  }
};
</script>
<style lang="scss">
.history-detail {
  .left{
    width:50%;
  }
  .right{
    width:50%;
    padding-left:8%;
    box-sizing: border-box;
  }
  .content {
    display: flex;
    justify-content: space-around;
    .line {
      width: 1px;
      background: #e8eaec;
      margin-top: -16px;
    }
    .ivu-form-item-label {
      font-size: 14px !important;
      text-align: left;
    }
    .ivu-form-item-content {
      label {
        font-size: 14px !important;
      }
    }
  }
}
</style>


