<template>
  <div class="add-production">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="150"
      class="form-content"
    >
      <FormItem label="业务类型" prop="busType">
        <Select v-model="formValidate.busType" placeholder="请输入业务类型"  @on-change="seleceByType()">
           <Option v-for="item in busTypes" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="业务名称" prop="busName">
         <Select v-model="formValidate.busName" placeholder="请输入业务名称">
          <Option v-for="(item,index) in busNames" :value="item" :key="index">{{item}}</Option>
        </Select>
      </FormItem>
       <FormItem label="产品类型" prop="proType">
         <Select v-model="formValidate.proType" placeholder="请输入产品类型" >
           <Option v-for="item in proTypes" :value="item.code" :key="item.code">{{item.name}}</Option>
         </Select>
      </FormItem>
      <FormItem label="产品代码" prop="proCode">
        <Input v-model="formValidate.proCode" placeholder="请输入产品代码"  :disabled="disabledFlag"></Input>
      </FormItem>
      <FormItem label="产品名称" prop="proName">
        <Input v-model="formValidate.proName" placeholder="请输入产品名称"></Input>
      </FormItem>
      <FormItem label="期限" prop="xxxx">
         <DatePicker
            :value="formValidate.term"
            format="yyyy-MM-dd"
            type="date"
            placement="bottom-end"
            placeholder="请选择时间"
            @on-change="formValidate.term=$event"
          ></DatePicker>
      </FormItem>
      <FormItem label="是否允许境外客户：" prop="abroad" style="width:315px">
        <RadioGroup v-model="formValidate.abroad">
          <Radio label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="是否业务投资境外：" prop="investmentMark" style="width:315px">
        <RadioGroup v-model="formValidate.investmentMark">
          <Radio label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <Row>
        <Col span="11">
          <FormItem label="所属业务部门" prop="department">
           <treeselect v-model="formValidate.department" :options="data1" placeholder="请选择所属业务部门" />
          </FormItem>
        </Col>
        <Col span="2">
          <span class="speical-span">
            <FormItem></FormItem>
          </span>
        </Col>
        <Col span="11">
          <FormItem label="成立日期">
            <DatePicker
             type="date"
             placeholder="请输入成立日期" 
             :value="formValidate.buildDate"
             @on-change="formValidate.buildDate=$event"
             ></DatePicker>
          </FormItem>
        </Col>
      </Row>
      <FormItem label="产品描述">
        <Input
          v-model="formValidate.proDescribe"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入产品描述"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
    <Modal :mask-closable="false"   v-model="switchModal" scrollable title="提示：">
      <div>信息保存成功！</div>
      <div slot="footer">
        <Button @click="cancelModal">取消</Button>
        <Button @click="switchAssessRisk">进行洗钱风险评估</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  //props: ["type"],
  props: { dataObj: Object },
  created(){  //修改第一个链接，取得更新界面
    let that = this;
    if (this.dataObj.id) {
      this.disabledFlag = true;
      let params = new FormData();
      params.append("id", this.dataObj.id);
      this.axios
        .post("/risk/pro/updatePro", params, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            this.formValidate = response.data;
            this.axios
            .post(`/risk/pro/getBusNameByType?busType=${this.formValidate.busType}`)
            .then(response =>{
                if(response.code == "200"){
                  this.busNames = response.data;
                }
            });
          }
        })
        .catch(error => {});
    }
    this.axios
      .get("/system/getDic/BUS_TYPE,PRO_TYPE")
      .then(response => {
        if (response.code == "200") {
          this.busTypes = response.data.BUS_TYPE;
          this.proTypes = response.data.PRO_TYPE;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          this.data1 = response.data;
          this.selectTreeCleanUp(this.data1);
        }
      })
      .catch(error => {});
  },
  data() {
  const validatorProCode = (rule, value, callback) =>{
      if (!this.disabledFlag) {
        let params = new FormData();
        params.append("proCode", value);
        this.axios
          .post("/risk/pro/getProCode", params, {
            headers: { "Content-Type": "multipart/form-data" }
          })
          .then(response => {
            if (response.data == "201") {
              callback(new Error("产品代码已存在"));
            } else {
              callback();
            }
          })
          .catch(error => {});
      } else {
        callback();
      }
    };
    const validaterDepartment = (rule, value, callback) => {
      if (this.formValidate.department == undefined) {
        callback(new Error("请选择提取机构"));
      } else {
        callback();
      }
    };
    return {
      switchModal: false,
      disabledFlag:false,
      formValidate: {
        busType: "",
        term: "",
        busName: "",
        abroad: "",
        investmentMark: "",
        proCode: "",
        proName: "",
        department: null,
        buildDate: "",
        proDescribe:""
      },
      proTypes:[],
      busNames:[],
      busTypes: [],
      data1: [],
      ruleValidate: {
        busType: [
          {
            required: true,
            message: "业务类型不能为空！",
            trigger: "change"
          }
        ],
        abroad: [
          {
            required: true,
            message: "境内外客户不能为空！",
            trigger: "change"
          }
        ],
        investmentMark: [
          {
            required: true,
            message: "业务投资境外不能为空！",
            trigger: "change"
          }
        ],
        // term:[
        //     {
        //       required: true,
        //       message: "期限不能为空！",
        //       trigger: "change"
        //     },
        // ],
        proCode: [
          {
            required: true,
            message: "产品代码不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 6,
            message: "长度最大不能超过6个字符",
            trigger: "blur"
          },
         {
            validator: validatorProCode,
            trigger: "xxxx"
          }
        ],
        proType: [
          {
            required: true,
            message: "产品类型不能为空！",
            trigger: "change"
          }
        ],
        proName: [
          {
            required: true,
            message: "产品名称不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 32,
            message: "长度最大不能超过32个字符",
            trigger: "blur"
          }
        ],
        busName: [
          {
            required: true,
            message: "业务名称不能为空！",
            trigger: "change"
          }
        ],
        department: [
          {
            required: true,
            message: "所属业务部门不能为空！",
            validator: validaterDepartment,
            trigger: "xxxx"
          }
        ],
        proDescribe: [
          {
            required: true,
            message: "产品描述不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 512,
            message: "产品描述不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {},
  watch: {
    "formValidate.department": function(newValue, oldValue){
      this.$refs["formValidate"].validateField("department");
    },
    switchModal(a) {
      if (a == false) {
        this.$emit("cancel", false);
      }
    }
  },
  methods: {
    cancelModal() {
      this.switchModal = false;
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    switchAssessRisk() {
      this.switchModal = false;
      this.$emit("switchAssessRisk",this.dataObj.id);
    },
    handleSubmit(name) { // 修改链接，保存
      let data = JSON.stringify(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
         this.axios
            .post("/risk/pro/saveProInfo", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.dataObj.id = response.data.id;
                this.switchModal = true;
                this.$Message.success("保存成功");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    },
    seleceByType(){
      let that = this;
      this.busTypes.forEach(element => {
        if(element.code == this.formValidate.busType){
            this.axios
            .post(`/risk/pro/getBusNameByType?busType=${this.formValidate.busType}`)
            .then(response =>{
                if(response.code == "200"){
                  this.busNames = response.data;
                  this.formValidate.busName = "";
                }
            });
        };
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.add-production {
  margin: 0 30px;
  & >>> .ivu-form-item-label {
    font-size: 14px !important;
  }
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .button-content {
      margin-left: -120px;
      text-align: center;
      .ivu-form-item-content{
        margin-left: 0!important;
      }
      button {
        margin-right: 20px;
        width: 85px;
        height: 40px;
        font-size: 14px;
        &:nth-child(1) {
          background: #31d9a4;
          border-color: #31d9a4;
        }
        &:nth-child(2) {
          background: #d5d5d5;
          border-color: #d5d5d5;
        }
      }
    }
  }
}
</style>


